




















import { Component, Prop, Vue } from "vue-property-decorator";
import CountryFlag from 'vue-country-flag'

@Component({
  components: {
    CountryFlag
  }
})
export default class Test1 extends Vue {
  @Prop() private msg?: string;
  private selected: string = "";
  private noSelected: boolean = false;
  private yesSelected: boolean = false;
  private yesSeletedTimeOut: boolean = false;

  private yesClick() {
    this.noSelected = false;
    this.yesSelected = true;
    this.yesSeletedTimeOut = false;

    setTimeout(() => {
      this.yesSelected = false;
      this.yesSeletedTimeOut = true;
      window.open('https://lmgtfy.com/?q=b%E1%BB%8B+h%C3%A2m+th%C3%AC+ph%E1%BA%A3i+l%C3%A0m+sao','_blank');
    }, 3000);
  }

  private noClick() {
    // let meg: string = this.$t("test1.wrong_ans").toString();
    this.$notify.error({
      title: "Error",
      message: this.$t("test1.wrong_ans").toString()
    });
    this.noSelected = true;
    this.yesSelected = false;
    this.yesSeletedTimeOut = false;
    this.selected = "";
  }
}
